// fonts
$default-fonts: Arial, sans-serif;

@include font('KelsonSans-Regular','KelsonSans-Regular');
@include font('MullerBold','MullerBold');
@include font('MullerExtraBold','MullerExtraBold');
@include font('MullerMedium','MullerMedium');
@include font('PFBeauSansPro-Bold','PFBeauSansPro-Bold');
@include font('PFBeauSansPro-Regular','PFBeauSansPro-Regular');
@include font('PFBeauSansPro-Thin','PFBeauSansPro-Thin');
@include font('ProximaNova-Black','ProximaNova-Black');
@include font('ProximaNova-Bold','ProximaNova-Bold');
@include font('ProximaNova-Regular','ProximaNova-Regular');

@mixin kelson {
	font-family: 'KelsonSans-Regular', $default-fonts; }
@mixin muller-bold {
	font-family: 'MullerBold', $default-fonts; }
@mixin muller-extrabold {
	font-family: 'MullerExtraBold', $default-fonts; }
@mixin muller-medium {
	font-family: 'MullerMedium', $default-fonts; }
@mixin sans-bold {
	font-family: 'PFBeauSansPro-Bold', $default-fonts; }
@mixin sans-regular {
	font-family: 'PFBeauSansPro-Regular', $default-fonts; }
@mixin sans-thin {
	font-family: 'PFBeauSansPro-Thin', $default-fonts; }
@mixin nova-black {
	font-family: 'ProximaNova-Black', $default-fonts; }
@mixin nova-bold {
	font-family: 'ProximaNova-Bold', $default-fonts; }
@mixin nova-regular {
	font-family: 'ProximaNova-Regular', $default-fonts; }

@mixin trs {
	transition: all 0.2s; }

@mixin smooth {
	-webkit-font-smoothing: antialiased; }


/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	min-width: 320px;
	@include nova-regular;
	font-size: 16px;
	color: #3c3c3c;
	line-height: 1.2; }

.wrapper {
	overflow: hidden;
	margin: 0 auto;
	min-height: 100%;
	min-width: 990px; }

.container {
	width: 990px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;
	@include pie-clearfix; }

/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.cfx {
	@include pie-clearfix; }

/* ===================================
	 Header
	 @mixin ================================== */

.header {
	padding: 20px 0 110px;
	background: url(../img/bg/header_bg.jpg) no-repeat top center;
	background-size: cover;
	&__col {
		&:nth-of-type(1) {
			float: left;
			padding-left: 8px; }
		&:nth-of-type(2) {
			float: right;
			text-align: right;
			padding-right: 13px; } }
	&--logo {
		display: inline-block;
		vertical-align: middle;
		margin-right: 5px; }
	&__slogan {
		display: inline-block;
		vertical-align: top;
		font-size: 14px;
		color: #fff;
		@include sans-regular; }
	&__phone {
		margin-top: -6px;
		display: block;
		vertical-align: middle;
		@include muller-extrabold;
		font-size: 24px;
		color: #fff;
		@include trs;
		&:hover {
			color: #f08924; } }
	&__btn-small {
		display: inline-block;
		margin-top: -3px;
		vertical-align: top;
		border-bottom: 1px dashed white;
		@include muller-medium;
		color: #fff;
		font-size: 16px;
		line-height: 1;
		@include trs;
		&:hover {
			color: #f08924;
			border-bottom: 1px dashed #F08924; } }
	&__text {
		position: relative;
		padding-top: 105px;
		clear: both;
		text-align: center;
		color: #fff;
		text-shadow: 0 1px 1px black;
		font-variant: small-caps;
		&::after {
			position: absolute;
			right: 58px;
			bottom: 30px;
			@include s('arrow_left');
			content: ''; } }
	&__title {
		margin-bottom: 10px;
		font-size: 48px;
		text-shadow: 0 1px 1px black;
		line-height: 1.1;
		@include nova-black;
		&-color {
			display: block;
			font-size: 36px; } }
	&__descr {
		margin-bottom: 5px;
		font-size: 30px;
		@include nova-bold;
		&-color {
			display: block;
			margin-top: 40px;
			@include nova-regular;
			font-size: 24px; } }
	&__company {
		margin-bottom: 25px;
		@include nova-black;
		font-size: 24px; }
	&__btn-lg {
		display: inline-block;
		padding: 21px 62px 23px;
		@include muller-bold;
		font-size: 24px;
		text-shadow: 0 2px 0 #c06800;
		font-variant: normal;
		background-color: white;
		background-image: linear-gradient(to top, #ff7e00 0%, #fdca00 100%);
		border-radius: 36px;
		box-shadow: 0 15px 20px rgba(0, 0, 0, 0.27), 0 7px 0 #c06800;
		@include smooth;
		@include trs;
		&:hover {
			background-image: linear-gradient(to bottom, #ff7e00 0%, #fdca00 100%); }
		&:active {
			transform: translateY(7px);
			box-shadow: none; } } }

.logo {
	display: inline-block;
	color: #ffffff;
	&__title {
		font-size: 36px;
		line-height: 1;
		@include kelson;
		&-color {
			color: #bdbdbd;
			@include trs; } }
	&:hover &__title-color {
		color: #f08924; }
	&__descr {
		position: relative;
		margin-top: -3px;
		font-size: 14px;
		color: #bdbdbd; }
	&--footer {
		color: #1375ff; }
	&--footer &__title-color {
		color: #63b7ff; } }

.action {
	position: absolute;
	left: -68px;
	bottom: -135px;
	width: 362px;
	height: 306px;
	font-size: 11px;
	border-radius: 5px;
	text-align: left;
	font-variant: normal;
	letter-spacing: 0.05em;
	line-height: 1.4;
	background: url(../img/bg/action_bg.png) no-repeat;
	color: #454545;
	text-shadow: none;
	@include sans-bold;
	@include pie-clearfix;
	&__title {
		margin: 90px 0 0 97px;
		font-size: 18px; }
	&__descr {
		width: 110px;
		margin: 5px 0 7px 97px; }
	&__terms {
		position: relative;
		left: 10px;
		float: right;
		width: 100px;
		margin-right: 105px;
		&-color {
			color: #ee3231; } } }


/* ===================================
	 Main
	 @mixin ================================== */
.main {
	letter-spacing: 0.01em; }

.features {
	padding: 20px 0 10px;
	text-align: left;
	font-size: 0;
	&__container {
		display: table; }
	&__item {
		display: table-cell;
		vertical-align: top;
		width: 33.33%;
		font-size: 14px;
		line-height: 1.2;
		text-align: left;
		height: 0;
		padding-left: 5px;
		padding-right: 5px;
		&-inner {
			padding: 12px 12px 24px;
			height: calc(100% + 36px);
			border: 10px solid #0167ff; }
		&:nth-child(3) &-inner {
			padding-left: 24px; }
		&:last-child {
			margin-right: 0; } }
	&__pic {
		width: 150px;
		height: 150px;
		margin: 0 auto 5px; }
	&__img {
		display: block;
		margin-left: auto;
		margin-right: auto; }
	&__list {
		margin-left: -5px;
		font-size: 15px; }
	&__list-item {
		position: relative;
		left: 5px;
		padding-left: 20px;
		margin-bottom: 15px;
		&::before {
			position: absolute;
			top: 3px;
			left: 0;
			@include s('checked');
			content: ''; } }
	&__title {
		margin-bottom: 10px;
		@include nova-black;
		font-size: 21px;
		text-align: center;
		@include smooth; } }

.subs {
	padding: 30px 0 33px;
	background-image: linear-gradient(to top, #64b8ff 0%, #06f 99%, #06f 100%);
	text-align: center;
	&__text {
		margin-bottom: 30px;
		position: relative;
		color: #fff;
		&::before {
			position: absolute;
			left: 105px;
			bottom: -14px;
			@include s('arrow_down');
			content: ''; } }
	&__title {
		margin-bottom: 10px;
		@include nova-black;
		font-size: 36px;
		letter-spacing: 0.01em; }
	&__descr {
		margin-top: -3px;
		font-size: 24px;
		letter-spacing: 0.01em; }
	&-form {
		display: table;
		margin: 0 auto;
		font-size: 0;
		text-align: center;
		&__col {
			position: relative;
			display: inline-block;
			margin-left: 20px;
			margin-right: 20px;
			width: 235px; } }
	&-form__field {
		width: 100%;
		border: 2px solid #f4f4f4;
		border-radius: 25px;
		background: transparent;
		padding: 14px 20px;
		@include nova-regular;
		font-size: 16px;
		color: #fff;
		& + & {
			margin-left: 37px; } }
	&-form__field::placeholder {
		color: #fff; }
	&-form__btn {
		display: inline-block;
		margin-top: 27px;
		padding: 20px 45px;
		@include muller-bold;
		font-size: 19px;
		color: #ffffff;
		text-shadow: 0 2px 0 #c06800;
		background-color: white;
		background-image: linear-gradient(to top, #ff7e00 0%, #fdca00 100%);
		border-radius: 29px;
		box-shadow: 0 15px 20px rgba(0, 0, 0, 0.27), 0 7px 0 #c06800;
		transition: all .2s ease;
		@include smooth;
		&:hover {
			transform: scale(1.1); }
		&:active {
			transform: translateY(7px);
			box-shadow: none; } }
	.form-error {
		position: absolute;
		z-index: 1;
		left: 30px;
		bottom: -15px;
		font-size: 12px;
		color: #fff;
		line-height: 1; } }

.subs-popup {
	display: none;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: 10px;
	background-color: #fff;
	&__inner {
		width: 485px;
		position: relative;
		padding: 20px 15px 20px; }
	&__thx {
		margin-bottom: 5px;
		font-size: 48px; }
	&__text {
		font-size: 18px; }
	&__close {
		position: absolute;
		right: 15px;
		top: 10px;
		font-size: 16px;
		text-decoration: underline;
		cursor: pointer; } }

/* ===================================
	 Footer
	 @mixin ================================== */

.footer {
	padding: 20px 0 0;
	min-width: 960px;
	font-size: 14px;
	letter-spacing: 0.05em;
	background: #cfcfcf;
	@include pie-clearfix;
	&__col {
		float: left;
		&:nth-of-type(1) {
			width: 350px;
			padding-left: 23px; }
		&:nth-of-type(2) {
			width: 280px;
			padding-left: 40px; }
		&:nth-of-type(3) {
			float: right;
			width: 280px;
			padding-left: 25px; } }
	&--logo {
		margin-top: -7px;
		letter-spacing: 0;
		.logo__descr {
			color: #858585; } }
	&__slogan {
		margin-top: 12px;
		margin-bottom: 20px;
		font-size: 16px;
		letter-spacing: 0; } }

.contact {
	&__dest {
		color: #868686;
		& + & {
			margin-top: 18px; } }
	&__address {
		@include nova-black;
		margin-bottom: 18px; } }
