$icons: (0:0);
$icons: map-merge($icons,(arrow_down: (X: -193px, Y:0px, W: 184px, H: 74px, TW: 377px, TH: 194px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(arrow_left: (X: 0px, Y:0px, W: 189px, H: 194px, TW: 377px, TH: 194px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(checked: (X: -193px, Y:-78px, W: 14px, H: 11px, TW: 377px, TH: 194px, IMG: '../img/icons.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
	$icon: map-get($icons, $icon);
	@return map-get($icon, $attr); }

@mixin sprite($iconName) {
	background-image: url(icon-attr($iconName, IMG));
	width: icon-attr($iconName, W);
	height: icon-attr($iconName, H);
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
	background-image: url(icon-attr($iconName, IMG));
	$width: icon-attr($iconName, W);
	$height: icon-attr($iconName, H);
	width: $width/2;
	height: $height/2;
	$x: icon-attr($iconName, X);
	$y: icon-attr($iconName, Y);
	background-position: $x/2 $y/2;
	$tw: icon-attr($iconName, TW);
	$th: icon-attr($iconName, TH);
	background-size: $tw/2 $th/2; }
@mixin sprite-position-retina($iconName) {
	$x: icon-attr($iconName, X);
	$y: icon-attr($iconName, Y);
	background-position: $x/2 $y/2; }


@mixin s($i) {
	@include sprite($i); }
@mixin sp($i) {
	@include sprite-position($i); }
@mixin sr($i) {
	@include sprite-retina($i); }
@mixin spr($i) {
	@include sprite-position-retina($i); }

.icon-arrow_down {
	width: 184px;
	height: 74px;
	background-image: url('../img/icons.png');
	background-position: -193px 0px;
	background-size: 377px 194px; }
.icon-arrow_left {
	width: 189px;
	height: 194px;
	background-image: url('../img/icons.png');
	background-position: 0px 0px;
	background-size: 377px 194px; }
.icon-checked {
	width: 14px;
	height: 11px;
	background-image: url('../img/icons.png');
	background-position: -193px -78px;
	background-size: 377px 194px; }
